import React from 'react';

import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import { resable } from '@vl/redata/RESABLE';
import RESABLE from '@vl/redata/RESABLE.macro';
import AdvisorListX from '@uz/unitz-components-web/AdvisorListX';
import CategoryListX from '@uz/unitz-components-web/CategoryListX';
import HeroSection from '@uz/unitz-components-web/HeroSection';
import AdvisorSearchForm from '@uz/unitz-components-web/AdvisorSearchForm';
import FAQsSection from '@uz/unitz-components-web/FAQsSection';
import HandBookSection from '@uz/unitz-components-web/HandBookSection';
import UserGuideSection from '@uz/unitz-components-web/UserGuideSection';
import TestimonialSection from '@uz/unitz-components-web/TestimonialSection';
import WhyUsSection from '@uz/unitz-components-web/WhyUsSection';

const Index = () => {
  return (
    <DIV className="pageContainer">
      <div className="relative none-container">
        <HeroSection />
        <div className="absolute w-full bottom-24">
          <RESABLE>
            <DIV>
              {ctx.debug(() => {
                const categories = resable.for('categories')(ctx.get('categories'), []);
                ctx.set('categories', categories);
              })}
              <AdvisorSearchForm />
            </DIV>
          </RESABLE>
        </div>
      </div>
      <div className="py-0 wrapper app-row">
        <RESABLE>
          <DIV>
            {ctx.debug(() => {
              const advisorsList = resable.for('highlightAdvisors')(ctx.get('highlightAdvisors'), []);
              ctx.set('advisorsList', advisorsList);
            })}
            <AdvisorListX />
          </DIV>
        </RESABLE>
        <div className="border-b-2" />
        <RESABLE>
          <DIV>
            {ctx.debug(() => {
              const categories = resable.for('ctfCategories')(ctx.get('ctfCategories'), []);
              ctx.set('ctfCategories', categories);
            })}
            <CategoryListX />
          </DIV>
        </RESABLE>
        <div className="border-b-2" />
        <FAQsSection />
        <div className="border-b-2" />
        <HandBookSection />
        <div className="border-b-2" />
        <UserGuideSection />
        <TestimonialSection />
        <WhyUsSection />
        <RESABLE>
          <DIV>
            {ctx.debug(() => {
              const onlineAdvisors = resable.for('onlineAdvisors')(ctx.get('onlineAdvisors'), []);
              const newAdvisors = resable.for('newAdvisors')(ctx.get('newAdvisors'), []);
              ctx.set('advisorsMap', { newAdvisors, onlineAdvisors });
            })}
          </DIV>
        </RESABLE>
      </div>
    </DIV>
  );
};

export default Index;
