import React from 'react';

import DIV from '@vl/redata/DIV.macro';
import { Row, Col } from 'antd';
import { Avatar } from '@uz/unitz-components-web/Avatar';
import { ctx } from '@vl/redata';
import _ from 'lodash';
import styled from 'styled-components';
import gstyles from '@vl/gstyles';

const CustomStepName = styled.div`
  div {
    p {
      font-size: 16px;
      font-weight: 600;
      margin-bottom: 0;
      color: ${gstyles.colors.main};
    }
  }
`;

const CustomParagraph = styled.div`
  div {
    p {
      font-size: 14px;
      font-weight: 400;
      margin-bottom: 0;
      color: ${gstyles.colors.main};
    }
  }
`;

const View10 = () => {
  return (
    <DIV className="guideline">
      <div className="my-16">
        <h2 className="mb-8 text-4xl font-semibold text-center text-main">
          {ctx.get('sectionData.section.shortText')}
        </h2>
        <Row
          gutter={{
            xs: 8,
            sm: 16,
            md: 24,
            lg: 41,
          }}
        >
          {ctx.get('sectionData.children', []).map((step) => {
            return (
              <Col key={step.id} className="mb-3 gutter-row sm:mb-0" lg={6} sm={24} md={12}>
                <div className="h-full px-4 py-6 text-center border rounded-xl">
                  <h4 className="mb-4 text-xl font-semibold text-center text-brandb500">{_.get(step, 'shortText')}</h4>
                  <div className="mb-4">
                    <Avatar
                      shape="square"
                      size={{
                        xs: 50,
                        sm: 55,
                        md: 80,
                        lg: 64,
                        xl: 104,
                        xxl: 120,
                      }}
                      src={ctx.apply('ctf.findImage', step, 'image', '', { width: 620 })}
                    />
                  </div>
                  <CustomStepName>
                    {ctx.apply('ctf.renderHTMLText', { name: step.name }, 'longText.longText', {
                      className: 'mb-2',
                      as: 'div',
                    })}
                  </CustomStepName>

                  <CustomParagraph>
                    {ctx.apply('ctf.renderHTMLText', { name: step.name }, 'detailText.detailText', {
                      className: 'text-center text-ellipsis-4',
                      as: 'div',
                    })}
                  </CustomParagraph>
                </div>
              </Col>
            );
          })}
        </Row>
      </div>
    </DIV>
  );
};

export default View10;
