import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import _ from 'lodash';
import { Avatar } from '@uz/unitz-components-web/Avatar';

export const View8 = (props) => {
  const { itemData } = props;
  return (
    <DIV className="carouselItem">
      <div className="px-5 py-2 text-center">
        <div className="flex items-center justify-center mb-5">
          <Avatar
            size={{
              xs: 85,
              sm: 90,
              md: 100,
              lg: 64,
              xl: 60,
              xxl: 100,
            }}
            src={_.get(itemData, 'image.fixed.src', '')}
          />
        </div>
        <h5 className="text-base text-main text-semibold">{_.get(itemData, 'shortText', '')}</h5>
        {ctx.apply('ctf.renderHTMLText', { name: itemData.name }, 'longText.longText', {
          className: 'text-sm font-normal mb-7 text-sub',
          as: 'p',
        })}
        <p className="text-base font-normal text-center text-main">
          {ctx.apply('ctf.renderRichText', { name: itemData.name }, 'richText', null)}
        </p>
      </div>
    </DIV>
  );
};
export default View8;
