import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import _ from 'lodash';

import { useRenderProps } from '@vl/hooks/useRenderProps';

import AdvisorItemX from '@uz/unitz-components-web/AdvisorItemX';
import FlatList from '@uz/unitz-components-web/FlatList';
import { Row, Col, Button } from 'antd';

const renderItem = useRenderProps(
  ({ item, index }) => {
    return (
      <DIV forceCtx>
        {ctx.debug(() => {
          ctx.set('@item', item);
          ctx.set('@index', index);
        })}
        <div>
          <AdvisorItemX />
        </div>
      </DIV>
    );
  },
  ({ item, index }) => [index, _.get(item, 'id')]
);

const View8 = () => (
  <DIV className="component advisorListX 779BD3C8-CDF6-4067-A5A5-C1B320DFF758">
    <div className="my-16">
      <h2 className="mb-10 text-4xl font-semibold text-center text-main">
        {ctx.apply('i18n.t', 'Advisor.highlightAdvisorTitle')}
      </h2>
      <FlatList
        data={ctx.get('paginationModel.items', [])}
        renderItem={renderItem}
        keyExtractor={(item, index) => `${_.get(item, 'id')}_${index}`}
        horizontal
      />
      <Row className="justify-center h-12 mt-14" gutter={24}>
        {ctx.get('paginationModel.hasNextPage') && (
          <Col span={6}>
            <Button
              size="large"
              block
              onClick={ctx.get('paginationModel.onLoadMore')}
              loading={ctx.get('paginationModel.isLoading')}
              className={'flex flex-row items-center'}
            >
              <span className="mb-0 text-base font-semibold text-brandb500">
                {ctx.apply('i18n.t', 'Advisor.watchMore')}
              </span>
            </Button>
          </Col>
        )}
        <Col span={6}>
          <Button size="large" block onClick={ctx.get('advisorSearchForm.submitForm')}>
            <h5 className="mb-0 text-base font-semibold text-brandb500">{ctx.apply('i18n.t', 'Advisor.fitWithYou')}</h5>
          </Button>
        </Col>
      </Row>
    </div>
  </DIV>
);

export default View8;
