import React from 'react';
import DIV from '@vl/redata/DIV.macro';
import { ctx } from '@vl/redata';
import { Link } from '@uz/unitz-components-web/Link';
import { Row, Col, Button } from 'antd';
import styled from 'styled-components';
import gstyles from '@vl/gstyles';

const CustomText = styled.div`
  p > p {
    color: ${gstyles.colors.main};
    font-size: 16px;
    font-weight: 400;
  }
`;

const View8 = () => {
  return (
    <DIV className="questionList">
      <div className="my-16">
        <h2 className="mb-6 font-semibold text-main text-4xl text-center">
          {ctx.get('sectionData.section.shortText')}
        </h2>
        <Row className="justify-center mb-6">
          <Col lg={18} sm={24} className="text-center">
            <CustomText>
              {ctx.apply('ctf.renderHTMLText', { name: ctx.get('sectionData.section.name') }, 'longText.longText', {
                as: 'p',
              })}
            </CustomText>
          </Col>
        </Row>
        <div className="wrapper py-0 mb-10">
          <Row gutter={16} style={{ rowGap: '15px' }}>
            {ctx
              .get('sectionData.children', [])
              .slice(0, 6)
              .map((question) => {
                return (
                  <Col key={question.id} lg={12} sm={24}>
                    <Link className="list-item list-disc px-2" to={`/faqs#${question.slug}`}>
                      <p className="mb-0 font-normal text-base text-brandb500">{question.shortText}</p>
                    </Link>
                  </Col>
                );
              })}
          </Row>
        </div>
        <Row className="justify-center h-12" gutter={24}>
          <Col span={12}>
            <Link to="/faqs">
              <Button size="large" onClick={ctx.get('form.onSubmit')} block>
                <h5 className="mb-0 text-base font-semibold text-brandb500">{ctx.apply('i18n.t', 'Question.more')}</h5>
              </Button>
            </Link>
          </Col>
        </Row>
      </div>
    </DIV>
  );
};

export default View8;
